	module.exports = {"data":{"post":{"html":"<p>This week we installed signs on the Autobahn A1 a couple of km on either side of the Dibbersen exit. So whether you are coming from Bremen or from Hamburg and you didn't know about our McDonald's in Dibbersen, you will now.</p>\n<p>German restrictions on such signs are very strict, so compared to what you see on American interstates, the signs are small, low to the ground, and set quite far from the highway. They also are not cheap; we need a dozen additional guests each day for the signs to be worth the cost. If the past couple of days are any indication, the signs seem to be working, although business could also be up as a result of the cooler weather and the Euro promotion that just started (mmmm, mini-schnitzels).</p>\n<p>It's very difficult to take a picture of a small sign from a moving car, so for now I have to settle for showing the digital proof.</p>\n<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2008/05/a1signage.jpg\" alt=\"A1 Signage for Dibbersen\" border=\"0\" width=\"450\" height=\"338\" /></p>","fields":{"title":"Signs on the Autobahn","date":"17 May 2008","url":"https://papascott.de/2008-05-17-signs-on-the-autobahn/"}}},"pathContext":{"slug":"/2008-05-17-signs-on-the-autobahn/","prev":{"fields":{"title":"Coming Soon to a Carport Near Us","date":"15 May 2008","slug":"/2008-05-15-coming-soon-to-a-carport-near-us/"}},"next":{"fields":{"title":"Takeover Battle for Enodis","date":"19 May 2008","slug":"/2008-05-19-takeover-battle-for-enodis/"}}}}

